@import url("https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;
  width: 100%;
  min-height: 100vh;
  font-family: "Ubuntu", Arial, sans-serif;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: scroll;
  background-color: #f5f7fa;
}
body a {
  text-decoration: none;
}
body ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

polyline,
path {
  fill: inherit;
  stroke: inherit;
}

.container {
  min-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}
@media screen and (min-width: 601px) {
  .container {
    max-width: 601px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}
@media screen and (min-width: 992px) {
  .container {
    max-width: 992px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding: 0 15px;
  }
}

.header {
  min-height: 50px;
  background: #9564aa;
}
.header__main-block {
  display: none;
}
@media screen and (min-width: 768px) {
  .header__main-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    min-height: 100px;
    padding: 10px 0;
  }
}
.header__main-block__logo {
  font-size: 40px;
  font-weight: 700;
  color: #fff;
}
@media screen and (min-width: 992px) {
  .header__main-block__logo {
    font-size: 44px;
  }
}
@media screen and (min-width: 1200px) {
  .header__main-block__logo {
    font-size: 48px;
  }
}
.header__main-block__site-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}
@media screen and (min-width: 992px) {
  .header__main-block__site-list {
    gap: 35px;
  }
}
@media screen and (min-width: 1200px) {
  .header__main-block__site-list {
    gap: 40px;
  }
}
.header__main-block__site-list__item__link {
  font-size: 16px;
  font-weight: 700;
  color: #fff;
}
.header__main-block__site-list__item:hover > a {
  text-decoration: underline;
}
.header__main-block__site-list__item.premium {
  position: relative;
  padding: 5px 13px;
  padding-right: 40px;
  border-radius: 10px;
  background: #684677;
}
.header__main-block__site-list__item.premium::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 13px;
  z-index: 1;
  width: 18px;
  height: 15px;
  background-image: url("../img/logo-mobile-header.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  transform: translateY(-50%);
}
.header__main-block__site-list__item.premium a {
  color: #ffcf2d;
}
.header__main-block__site-list__item.premium:hover {
  transform: scale(1.05);
}
.header__main-block__site-list__item.premium:hover > a {
  text-decoration: underline;
}
.header__mobile-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  min-height: 50px;
  padding: 10px 0;
}
@media screen and (min-width: 768px) {
  .header__mobile-block {
    display: none;
  }
}
.header__mobile-block__menu {
  position: relative;
  width: 23px;
  height: 14px;
  cursor: pointer;
}
.header__mobile-block__menu::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 2px;
  background: #fff;
  box-shadow: 0 6px 0 0 #fff, 0 12px 0 0 #fff;
}
.header__mobile-block__logo-wrapper {
  width: 18px;
  height: 18px;
}
.header__mobile-block__logo-wrapper a.header__mobile-block__logo {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  position: relative;
  z-index: 1;
  flex-grow: 1;
  display: flex;
  background: #fff;
}
.main__scene {
  position: absolute;
  display: none;
  z-index: 0;
}
@media screen and (min-width: 992px) {
  .main__scene {
    display: block;
  }
}
.main__scene#first-scene {
  top: 144px;
  right: 65%;
}
.main__scene#second-scene {
  top: 32px;
  left: 67%;
}
.main > div.container {
  position: relative;
  z-index: 1;
}
@media screen and (min-width: 992px) {
  .main > div.container {
    display: flex;
    justify-content: space-between;
    gap: 30px;
  }
}
.main__page-title {
  margin: 15px 0 23px;
  border-bottom: 2px solid #9564aa;
}
@media screen and (min-width: 600px) {
  .main__page-title {
    margin: 20px 0;
  }
}
@media screen and (min-width: 768px) {
  .main__page-title {
    margin: 20px 0 25px;
  }
}
@media screen and (min-width: 992px) {
  .main__page-title {
    margin: 20px 0 30px;
    text-shadow: 4px 4px 0 rgba(0, 0, 0, 0.25);
  }
}
@media screen and (min-width: 1200px) {
  .main__page-title {
    font-size: 40px;
  }
}
.main__page-title h1.page-title {
  font-size: 20px;
  font-weight: 700;
  color: #000;
}
@media screen and (min-width: 600px) {
  .main__page-title h1.page-title {
    font-size: 24px;
  }
}
@media screen and (min-width: 768px) {
  .main__page-title h1.page-title {
    font-size: 28px;
  }
}
@media screen and (min-width: 992px) {
  .main__page-title h1.page-title {
    font-size: 34px;
    text-shadow: 4px 4px 0 rgba(0, 0, 0, 0.25);
  }
}
@media screen and (min-width: 1200px) {
  .main__page-title h1.page-title {
    font-size: 40px;
  }
}
.main__discount.discount {
  display: none;
  margin-bottom: 26px;
}
@media screen and (min-width: 768px) {
  .main__discount.discount {
    display: flex;
  }
}

.catalog {
  position: relative;
  width: 100%;
  max-width: 100%;
  margin-bottom: 32px;
}
.catalog__item {
  width: 100%;
  height: auto;
  min-height: 59px;
  font-size: 24px;
  font-weight: 700;
  color: #000;
  box-sizing: border-box;
  background: rgba(149, 100, 170, 0.1);
  border-radius: 10px;
  border: 2px solid #9564aa;
}
.catalog__item:last-of-type {
  background: rgba(170, 100, 100, 0.2);
  border: 2px solid #aa6464;
}
.catalog__item:has(.catalog__item__button:hover):not(.special-offer) {
  color: #fff;
  background: #9564aa;
}
.catalog__item-inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  height: 100%;
  min-height: 59px;
  padding: 13px 30px;
}
@media screen and (min-width: 768px) {
  .catalog__item-inner {
    flex-direction: row;
    flex-wrap: wrap;
    column-gap: 40px;
    row-gap: 11px;
    padding: 15px;
  }
}
.catalog__item-inner__content {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .catalog__item-inner__content {
    width: fit-content;
  }
}
.catalog__item-inner__content__credits {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 17px;
  height: 17px;
  font-size: 8px;
  font-weight: 700;
  color: #000;
  border-radius: 50%;
  background: #ffcf2d;
}
.catalog__item-inner__content__value {
  flex-shrink: 0;
}
@media screen and (min-width: 768px) {
  .catalog__item-inner__content__value {
    display: none;
  }
}
.catalog__item__button {
  display: none;
}
@media screen and (min-width: 600px) {
  .catalog__item__button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 52px;
    padding: 10px;
    margin-top: auto;
    font-size: 16px;
    font-weight: 700;
    color: #ccc;
    border-radius: 5px;
    border: none;
    background: #fff;
    cursor: pointer;
  }
}
@media screen and (min-width: 768px) {
  .catalog__item__button {
    max-width: 185px;
  }
}
.catalog__item__button:hover {
  color: #000;
  background: #ffcf2d;
}
.catalog__item.special-offer {
  padding-left: 20px;
  color: #fff;
  background: none;
  border: none;
}
.catalog__item.special-offer:has(.catalog__item__button:hover) {
  color: #fff;
}
@media screen and (min-width: 768px) {
  .catalog__item.special-offer {
    padding-left: 0;
    color: #000;
    background: rgba(149, 100, 170, 0.1);
    border-radius: 10px;
    border: 2px solid #9564aa;
  }
  .catalog__item.special-offer:has(.catalog__item__button:hover) {
    color: #fff;
    background: #9564aa;
  }
}
.catalog__item.special-offer .catalog__item-inner {
  position: relative;
  justify-content: end;
  padding-left: 105px;
  background: #9564aa;
  border: 2px solid #9564aa;
  border-radius: 10px;
}
@media screen and (min-width: 768px) {
  .catalog__item.special-offer .catalog__item-inner {
    justify-content: center;
    padding-left: 15px;
    background: none;
    border: none;
    border-radius: 0;
  }
}
.catalog__item.special-offer .catalog__item-inner > span.special-offer__card {
  position: absolute;
  top: 50%;
  left: -20px;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3px 10px;
  font-size: 14px;
  font-weight: 700;
  color: #000;
  border-radius: 5px;
  background: #ffcf2d;
  transform: translateY(-50%);
}
@media screen and (min-width: 768px) {
  .catalog__item.special-offer .catalog__item-inner > span.special-offer__card {
    display: none;
    top: -12px;
    left: 50%;
    width: 123px;
    transform: translateX(-50%);
  }
}
.catalog__item.special-offer .catalog__item-inner__content {
  justify-content: end;
}
@media screen and (min-width: 600px) {
  .catalog__item.special-offer .catalog__item-inner__content {
    justify-content: center;
  }
}
@media screen and (min-width: 600px) {
  .catalog__item.special-offer .catalog__item-inner > .catalog__item__button {
    color: #000;
    background: #ffcf2d;
  }
}
@media screen and (min-width: 768px) {
  .catalog__item.special-offer .catalog__item-inner > .catalog__item__button {
    color: #ccc;
    background: #fff;
  }
}
.catalog__item.special-offer .catalog__item-inner > .catalog__item__button:hover {
  color: #000;
  background: #ffcf2d;
}
.catalog__item.special-offer .catalog__item-inner > .catalog__item__button:hover ~ span.special-offer__card {
  display: flex;
}
.catalog__item.swiper-slide-active {
  visibility: visible;
}
@media screen and (min-width: 600px) {
  .catalog__item.swiper-slide.swiper-slide-next {
    visibility: visible;
  }
}
.catalog__item:not(.swiper-slide-active) {
  visibility: hidden;
}
@media screen and (min-width: 768px) {
  .catalog__item:not(.swiper-slide-active) {
    visibility: visible;
  }
}
.catalog > div.swiper-container {
  position: relative;
  padding-bottom: 50px;
  overflow-x: hidden;
}
@media screen and (min-width: 992px) {
  .catalog > div.swiper-container {
    margin-bottom: 0;
  }
}
@media screen and (min-width: 768px) {
  .catalog > div.swiper-container > .swiper-wrapper {
    flex-wrap: wrap;
    justify-content: space-between;
    column-gap: 30px;
    row-gap: 15px;
  }
}
@media screen and (min-width: 768px) {
  .catalog > div.swiper-container > .swiper-wrapper > .swiper-slide {
    flex-basis: 150px;
    flex-grow: 1;
    height: auto;
  }
}
.catalog > div.swiper-container > div.swiper-pagination {
  bottom: 32px;
}
.catalog > div.swiper-container > div.swiper-pagination > span.swiper-pagination-bullet-active, .catalog > div.swiper-container > div.swiper-pagination > span.swiper-pagination-bullet {
  background-color: #ffc500;
  opacity: 1;
}
@media screen and (min-width: 768px) {
  .catalog > div.swiper-container > div.swiper-pagination {
    display: none;
  }
}
.catalog > .catalog__button-mobile {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 55px;
  padding: 10px;
  font-size: 16px;
  font-weight: 700;
  color: #000;
  border-radius: 10px;
  border: none;
  background: #ffcf2d;
  cursor: pointer;
}
.catalog > .catalog__button-mobile:hover {
  background: #ffd650;
  text-decoration: underline;
}
@media screen and (min-width: 600px) {
  .catalog > .catalog__button-mobile {
    display: none;
  }
}
.catalog__privacy {
  display: flex;
  gap: 10px;
  margin-top: 15px;
}
.catalog__privacy--checkbox__input {
  appearance: none;
  position: relative;
  width: 20px;
  height: 20px;
  background: #9564aa;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.catalog__privacy--checkbox__input::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 20px;
  height: 20px;
  font-size: 14px;
}
.catalog__privacy--checkbox__input:checked::after {
  background-image: url("../img/tick.png");
  background-repeat: no-repeat;
  background-position: center;
}
.catalog__privacy--content {
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
  color: #000;
}
@media screen and (min-width: 600px) {
  .catalog__privacy--content {
    font-size: 12px;
    line-height: 18px;
  }
}
.catalog__privacy--content a {
  text-decoration: none;
  color: #9564aa;
}
.catalog__privacy--content a:hover {
  text-decoration: underline;
}

@media screen and (min-width: 992px) {
  .possibilities {
    display: flex;
    justify-content: center;
  }
}
.possibilities__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 13px;
}
@media screen and (min-width: 992px) {
  .possibilities__wrapper {
    display: grid;
    grid-template-columns: 78px 83px 108px 94px;
    grid-template-rows: 1fr auto 1fr;
    grid-template-areas: ". icon1 icon2 ." "icon3 logo logo icon4" ". icon5 icon6 .";
    align-items: flex-start;
    gap: 15px;
  }
}
.possibilities__logo {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 992px) {
  .possibilities__logo {
    grid-area: logo;
    align-self: center;
  }
}
@media screen and (min-width: 992px) {
  .possibilities__logo img {
    width: 129px;
    height: 107px;
  }
}
.possibilities__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(202px, 1fr));
  gap: 10px;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .possibilities__list {
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  }
}
@media screen and (min-width: 992px) {
  .possibilities__list {
    display: contents;
  }
}
.possibilities__list__item {
  display: flex;
  align-items: center;
  gap: 10px;
}
@media screen and (min-width: 992px) {
  .possibilities__list__item {
    flex-direction: column;
    gap: 4px;
  }
}
@media screen and (min-width: 992px) {
  .possibilities__list__item:nth-child(1) {
    grid-area: icon1;
  }
}
@media screen and (min-width: 992px) {
  .possibilities__list__item:nth-child(2) {
    grid-area: icon4;
    margin-left: -40px;
  }
}
@media screen and (min-width: 992px) {
  .possibilities__list__item:nth-child(3) {
    grid-area: icon3;
    margin-right: -40px;
  }
}
@media screen and (min-width: 992px) {
  .possibilities__list__item:nth-child(4) {
    grid-area: icon2;
  }
}
@media screen and (min-width: 992px) {
  .possibilities__list__item:nth-child(5) {
    grid-area: icon5;
  }
}
@media screen and (min-width: 992px) {
  .possibilities__list__item:nth-child(6) {
    grid-area: icon6;
  }
}
.possibilities__list__item:nth-child(n+5) {
  display: none;
}
@media screen and (min-width: 992px) {
  .possibilities__list__item:nth-child(n+5) {
    display: flex;
  }
}
.possibilities__list__item__icon-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #f5f0f7;
}
@media screen and (min-width: 992px) {
  .possibilities__list__item__icon-wrapper {
    width: 50px;
    height: 50px;
  }
}
.possibilities__list__item__icon-wrapper:hover > svg > use {
  fill: #ffcf2d;
}
.possibilities__list__item__icon {
  width: 16px;
  height: 16px;
}
@media screen and (min-width: 992px) {
  .possibilities__list__item__icon {
    width: 24px;
    height: 24px;
  }
}
.possibilities__list__item__icon > use {
  fill: #9564aa;
}
.possibilities__list__item__text {
  padding: 10px;
  font-size: 12px;
  font-weight: 700;
  color: #9564aa;
}
@media screen and (min-width: 992px) {
  .possibilities__list__item__text {
    padding: 0;
    text-align: center;
  }
}

.sidebar {
  display: none;
  margin-top: 30px;
  padding: 18px;
  background: rgba(149, 100, 170, 0.1);
  border: 2px solid;
  border-color: rgba(149, 100, 170, 0.2);
  border-radius: 10px;
}
@media screen and (min-width: 992px) {
  .sidebar {
    display: flex;
    flex-direction: column;
    gap: 20px;
    flex-basis: 270px;
    flex-shrink: 0;
  }
}
.sidebar__user {
  display: flex;
  align-items: center;
  gap: 15px;
  padding-bottom: 20px;
  border-bottom: 2px solid #e2d4e8;
}
.sidebar__user__avatar {
  border-radius: 50%;
  overflow: hidden;
}
.sidebar__user__avatar__image {
  width: 49px;
  height: 49px;
  object-fit: cover;
  object-position: center;
}
.sidebar__user__info {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.sidebar__user__info__username {
  font-size: 16px;
  font-weight: 700;
  color: #000;
}
.sidebar__user__info__button {
  width: 86px;
  min-height: 21px;
  font-size: 10px;
  font-weight: 400;
  text-transform: uppercase;
  color: #fff;
  border-radius: 5px;
  background: #684677;
  border: none;
  cursor: pointer;
}
.sidebar__user__info__button:hover {
  transform: scale(1.05);
}
.sidebar__menu__list {
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.sidebar__menu__list__item {
  display: flex;
  align-items: center;
  gap: 30px;
}
.sidebar__menu__list__item:hover > a {
  text-decoration: underline;
  color: #684677;
}
.sidebar__menu__list__item__icon {
  width: 22px;
  height: 22px;
}
.sidebar__menu__list__item__icon > use {
  fill: #9564aa;
}
.sidebar__menu__list__item__link {
  flex-grow: 1;
  display: block;
  height: 100%;
  font-size: 16px;
  font-weight: 400;
  color: #000;
}

.discount {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  width: 100%;
  min-height: 82px;
  padding: 13px 10px;
  background: #c15ca1;
  border-radius: 10px;
}
@media screen and (min-width: 1200px) {
  .discount {
    gap: 20px;
  }
}
.discount__value {
  width: 31%;
  font-size: 30px;
  font-weight: 700;
  letter-spacing: 1px;
  color: #fff;
  -webkit-text-fill-color: #ffcf2d;
  -webkit-text-stroke: 1px #fff;
}
@media screen and (min-width: 1200px) {
  .discount__value {
    font-size: 40px;
  }
}
.discount__offer-available {
  width: 26%;
  font-size: 16px;
  font-weight: 700;
  color: #fff;
}
@media screen and (min-width: 992px) {
  .discount__offer-available {
    width: 28%;
  }
}
@media screen and (min-width: 1200px) {
  .discount__offer-available {
    width: 34%;
  }
}
.discount__timer {
  display: flex;
  align-items: center;
  gap: 10px;
  min-height: 56px;
  padding: 5px 10px;
  font-size: 40px;
  font-weight: 400;
  color: #000;
  background: #fff;
  border-radius: 5px;
}
